/* global MktoForms2 */

/*
 * Redirect to support form and disable form fields
 */
(function () {
	"use strict";

	MktoForms2.whenReady(function () {
		var requestField = document.getElementById("Call_Request_Options__c");

		requestField.addEventListener("change", function () {
			var selectedOption = requestField.options[requestField.selectedIndex].value;

			if (selectedOption === "Technical support") {
				window.location.href = "/forms/contact-support/";

				var selects = document.querySelectorAll("select");
				Array.prototype.forEach.call(selects, function(el){
					el.setAttribute('disabled', true);
					el.classList.add("disabled");
				});

				var inputs = document.querySelectorAll("input");
				Array.prototype.forEach.call(inputs, function(el){
					el.setAttribute('disabled', true);
					el.classList.add("disabled");
				});

				var textareas = document.querySelectorAll("textarea");
				Array.prototype.forEach.call(textareas, function(el){
					el.setAttribute('disabled', true);
					el.classList.add("disabled");
				});

				var communicationConsents = document.querySelectorAll(".communicationConsent");
				Array.prototype.forEach.call(communicationConsents, function(el){
					el.classList.add("disabled");
				});

				var mktoButtons = document.querySelectorAll(".mktoButton");
				Array.prototype.forEach.call(mktoButtons, function(el){
					el.setAttribute('disabled', true);
					el.classList.add("disabled");
					el.textContent = "";
				});
			}
		});
	});

}(MktoForms2));